







import {Component, Prop, Vue} from 'vue-property-decorator'

@Component({
  name: "password-input"
})
export default class App extends Vue {
  @Prop(Boolean) error = false
  @Prop(String) value = ''

  private show = false

  get input() {
    return this.value
  }

  set input(val) {
    this.$emit('input', val)
  }
}
